export const mapToBannerSrc = (map) => {
    const mapToBanner = {
        ancient: "ancient.jpg",
        cache: "cache.jpg",
        dustii: "dust_II.png",
        inferno: "inferno.jpg",
        mirage: "mirage.jpg",
        nuke: "nuke.jpg",
        office: "office.jpg",
        overpass: "overpass.jpg",
        train: "train.jpg",
        vertigo: "vertigo.jpg",
    };
    return ("/images/map_banners/" + mapToBanner[map.toLowerCase().replace(" ", "")]);
};
